"use client"

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Switch } from '@/components/ui/switch'
import { Separator } from '@/components/ui/separator'
import { MobileNavigation } from '@/components/indiko/navigation'
import { Mascot } from '@/components/indiko/mascot'
import Link from 'next/link'

export default function PerfilPage() {
  const [userInfo, setUserInfo] = useState({
    nome: 'João Silva',
    email: 'joao@email.com',
    whatsapp: '(11) 99999-9999'
  })

  const [notifications, setNotifications] = useState({
    indicacoes: true,
    recompensas: true,
    ranking: false,
    promocoes: true
  })

  const [isEditing, setIsEditing] = useState(false)
  const [isSaving, setIsSaving] = useState(false)

  const handleInputChange = (field: string, value: string) => {
    setUserInfo(prev => ({ ...prev, [field]: value }))
  }

  const handleNotificationChange = (field: string, value: boolean) => {
    setNotifications(prev => ({ ...prev, [field]: value }))
  }

  const handleSave = async () => {
    setIsSaving(true)
    // Simular salvamento
    setTimeout(() => {
      setIsSaving(false)
      setIsEditing(false)
      // Aqui seria a integração com o backend
    }, 1500)
  }

  const handleLogout = () => {
    // Aqui seria a lógica de logout
    if (confirm('Tem certeza que deseja sair?')) {
      window.location.href = '/'
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-white pb-20">
      {/* Header */}
      <div className="bg-white shadow-sm">
        <div className="px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-3">
              <Link href="/dashboard">
                <Button variant="ghost" size="sm" className="rounded-full">
                  ←
                </Button>
              </Link>
              <div>
                <h1 className="font-poppins font-bold text-xl text-gray-900">
                  Meu Perfil
                </h1>
                <p className="text-sm text-gray-600">
                  Gerencie suas informações
                </p>
              </div>
            </div>
            
            <Mascot size="sm" animated={true} />
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="px-6 py-6 space-y-6">
        {/* Profile Header */}
        <Card className="bg-gradient-to-r from-blue-600 to-blue-700 text-white border-0 shadow-lg">
          <CardContent className="p-6 text-center">
            <div className="w-20 h-20 bg-white bg-opacity-20 rounded-full mx-auto mb-4 flex items-center justify-center">
              <span className="text-2xl font-bold text-white">
                {userInfo.nome.split(' ').map(n => n[0]).join('').toUpperCase()}
              </span>
            </div>
            <h2 className="font-poppins font-bold text-xl mb-1">
              {userInfo.nome}
            </h2>
            <p className="text-blue-100">
              Membro desde Janeiro 2024
            </p>
          </CardContent>
        </Card>

        {/* Personal Information */}
        <Card className="shadow-md">
          <CardHeader className="flex flex-row items-center justify-between">
            <CardTitle className="font-poppins font-bold text-lg">
              Informações Pessoais
            </CardTitle>
            <Button
              onClick={() => setIsEditing(!isEditing)}
              variant="outline"
              size="sm"
              className="rounded-full"
            >
              {isEditing ? 'Cancelar' : 'Editar'}
            </Button>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="space-y-2">
              <Label htmlFor="nome" className="font-semibold">Nome completo</Label>
              <Input
                id="nome"
                type="text"
                value={userInfo.nome}
                onChange={(e) => handleInputChange('nome', e.target.value)}
                disabled={!isEditing}
                className="h-12 rounded-xl border-2 disabled:bg-gray-50"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="email" className="font-semibold">E-mail</Label>
              <Input
                id="email"
                type="email"
                value={userInfo.email}
                onChange={(e) => handleInputChange('email', e.target.value)}
                disabled={!isEditing}
                className="h-12 rounded-xl border-2 disabled:bg-gray-50"
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="whatsapp" className="font-semibold">WhatsApp</Label>
              <Input
                id="whatsapp"
                type="tel"
                value={userInfo.whatsapp}
                onChange={(e) => handleInputChange('whatsapp', e.target.value)}
                disabled={!isEditing}
                className="h-12 rounded-xl border-2 disabled:bg-gray-50"
              />
            </div>

            {isEditing && (
              <Button
                onClick={handleSave}
                disabled={isSaving}
                className="w-full h-12 bg-blue-600 hover:bg-blue-700 text-white font-semibold rounded-xl mt-4"
              >
                {isSaving ? (
                  <div className="flex items-center space-x-2">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    <span>Salvando...</span>
                  </div>
                ) : (
                  'Salvar Alterações'
                )}
              </Button>
            )}
          </CardContent>
        </Card>

        {/* Notifications Settings */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Configurações de Notificações
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-gray-900">Indicações</p>
                <p className="text-sm text-gray-600">Receber notificações sobre suas indicações</p>
              </div>
              <Switch
                checked={notifications.indicacoes}
                onCheckedChange={(value) => handleNotificationChange('indicacoes', value)}
              />
            </div>

            <Separator />

            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-gray-900">Recompensas</p>
                <p className="text-sm text-gray-600">Avisos sobre novas recompensas disponíveis</p>
              </div>
              <Switch
                checked={notifications.recompensas}
                onCheckedChange={(value) => handleNotificationChange('recompensas', value)}
              />
            </div>

            <Separator />

            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-gray-900">Ranking</p>
                <p className="text-sm text-gray-600">Atualizações sobre sua posição no ranking</p>
              </div>
              <Switch
                checked={notifications.ranking}
                onCheckedChange={(value) => handleNotificationChange('ranking', value)}
              />
            </div>

            <Separator />

            <div className="flex items-center justify-between">
              <div>
                <p className="font-semibold text-gray-900">Promoções</p>
                <p className="text-sm text-gray-600">Ofertas especiais e campanhas</p>
              </div>
              <Switch
                checked={notifications.promocoes}
                onCheckedChange={(value) => handleNotificationChange('promocoes', value)}
              />
            </div>
          </CardContent>
        </Card>

        {/* Account Actions */}
        <Card className="shadow-md">
          <CardHeader>
            <CardTitle className="font-poppins font-bold text-lg">
              Conta
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Button
              variant="outline"
              className="w-full h-12 border-2 rounded-xl font-semibold"
            >
              Mudar Senha
            </Button>

            <Link href="/ajuda">
              <Button
                variant="outline"
                className="w-full h-12 border-2 rounded-xl font-semibold"
              >
                Central de Ajuda
              </Button>
            </Link>

            <Button
              onClick={handleLogout}
              variant="destructive"
              className="w-full h-12 rounded-xl font-semibold"
            >
              Sair da Conta
            </Button>
          </CardContent>
        </Card>

        {/* App Info */}
        <Card className="bg-gray-50 border-gray-200 shadow-sm">
          <CardContent className="p-4 text-center">
            <p className="text-sm text-gray-600 mb-2">
              Indiko App v1.0.0
            </p>
            <p className="text-xs text-gray-500">
              Desenvolvido com ❤️ para conectar pessoas
            </p>
          </CardContent>
        </Card>
      </div>

      <MobileNavigation />
    </div>
  )
}
